<template>
  <div class="mobile-container">
    <Header/>

    <div class="m-content">
        <div class="m-head">
            <div class="m-item">
                <div class="item-lebel">{{ nodeData.total_node}}</div>
                <p>{{ $t('lang.dpos_title1') }}</p>
            </div>
            <div class="m-item">
                <div class="item-lebel">{{ nodeData.total_user}}</div>
                <p>{{ $t('lang.dpos_title2') }}</p>
            </div>
            <div class="m-item">
                <div class="item-lebel">{{ nodeData.total_amount}}</div>
                <p>{{ $t('lang.dpos_title3') }}</p>
            </div>
            <div class="m-item">
                <div class="item-lebel">{{ nodeData.total_divident }}</div>
                <p>{{ $t('lang.dpos_title4') }}</p>
            </div>
        </div>
        <div class="m-tips">
          <el-button @click="showNodeOrReward('node')" :class="{navtitle: nodePage}">{{ $t('lang.dpos_node_title') }}</el-button>
          <el-button @click="showNodeOrReward('reward')" :class="{navtitle: rewardPage}">{{ $t('lang.dpos_reward_title1') }}</el-button>
        </div>
        <div class="pos-list" v-show="rewardPage">
          <div class="item" style="background:rgba(243,244,247,1);">
            <div class="item-head">
              <div class="head-info">
                <div>{{ $t('lang.dpos_reward_title2') }}: {{ $t('lang.dpos_reward_title3') }} <span class="navtitle">{{ firstheight }}</span> {{ $t('lang.dpos_reward_title4') }} <span class="navtitle">{{ lastheight }}</span></div>
              </div>
            </div>
            <div class="item-reward">
              <div class="con-head" style="justify-content: space-around;">
                <span style="width: 50%" @click="tableSortChange('last_season_reward')" class="season">{{ $t('lang.dpos_reward_table3') }} <img src="@/assets/img/def.png" alt="" style="width: 22px;height: 22px;vertical-align: middle;"></span>
                <span  class="time reward" style="width: 50%" @click="tableSortChange('expected_reward')">{{ $t('lang.dpos_reward_table6') }} <img src="@/assets/img/def.png" alt="" style="width: 22px;height: 22px;vertical-align: middle;"></span>
              </div>
            </div>
          </div>
        </div>
        <div class="pos-list" v-show="nodePage">
            <div class="item" v-for="item in tableData" @click="tomagentList(item.agent_id)">
                <div class="item-head">
                    <div class="head-info">
                        <div class="lable">{{ $t('lang.dpos_node_table2') }}:</div>
                        <div class="con" style="color:#005DA6;">{{item.name}}</div>
                    </div>
                    <div class="head-info">
                        <div class="lable">{{ $t('lang.dpos_node_table1') }}:</div>
                        <div class="con">{{item.show_agent_id}}</div>
                    </div>
                </div>
                <div class="item-info">
                    <div class="con-head item-con">
                        <span style="width:50px;text-align: left;">{{item.generated_block_amount72}}</span>
                        <span class="time">{{filterDate(item.last_block_time)}}</span>
                        <span style="flex: 1;">{{item.amount}}</span>
                    </div>
                    <div class="con-head">
                        <span style="width:50px;text-align: left;">{{ $t('lang.dpos_node_table3') }}</span>
                        <span  class="time">{{ $t('lang.dpos_node_table6') }}</span>
                        <span style="flex: 1;">{{ $t('lang.dpos_node_table4') }}</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="pos-list" v-show="rewardPage">
          <div class="item" v-for="item in rewardData">
            <div class="item-head">
              <div class="head-info">
                <div class="lable-reward">{{ $t('lang.dpos_reward_table1') }}:</div>
                <div class="con" style="color:#005DA6;">{{item.id}}</div>
              </div>
              <div class="head-info">
                <div class="lable-reward">{{ $t('lang.dpos_reward_table2') }}:</div>
                <div class="con">{{item.name}}</div>
              </div>
              <div class="head-info">
                <div class="lable-reward">{{ $t('lang.dpos_reward_table3') }}:</div>
                <div class="con">{{item.last_season_reward}}</div>
              </div>
              <div class="head-info">
                <div class="lable-reward">{{ $t('lang.dpos_reward_table4') }}</div>
                <div class="con">{{item.amount}}</div>
              </div>
            </div>
            <div class="item-reward">
              <div class="con-head item-con">
                <span>{{item.current_season_dividend}}</span>
                <span class="time">{{ item.expected_reward }}</span>
              </div>
              <div class="con-head">
                <span>{{ $t('lang.dpos_reward_table5') }}</span>
                <span  class="time">{{ $t('lang.dpos_reward_table6') }}</span>
              </div>
            </div>
          </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '@/components/MHeader'
import Footer from '@/components/MFooter'
import pageSize from '@/components/MPage'
import dateFns from 'date-fns'
export default {
    components: {
        Header,
        Footer,
        pageSize
    },
    data() {
        return {
            tableData: [],
            rewardData: [],
            firstheight: "",
            lastheight: "",
            nodeData: [],
            nodePage: true,
            rewardPage: false,
            sorttype: "",
            seasontype: "",
            rewardtype: "",
            level: 27,
            isRouterAlive: true
        }
    },
    provide(){
        return {
            reload: this.reload
        }
    },
    computed: {},
    mounted() {
            this.getNodeList()
            this.getRewardList()
        },
        methods: {
            getNodeList(){
                this.$http.get("api/node/agent").then(res => {
                    if (res.data.code === 200){
                        this.tableData = res.data.data.list
                        this.nodeData = res.data.data.node
                    }
                })
            },
            getRewardList(){
                this.$http.post("api/node/reward", {sorttype: this.sorttype}).then(res => {
                    if (res.data.code === 200){
                        this.rewardData = res.data.data.list
                        this.firstheight = res.data.data.first
                        this.lastheight = res.data.data.last
                    }
                })
            },
            tomagentList(agent_id){
                this.$router.push({path:'/magentList',query:{nodeid:agent_id}})
            },
            filterDate(value, type) {
                if (!value) return ''
                if(value > 0){
                    value = value * 1000
                }
                const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
                return dateFns.format(value, filterType)
            },
            showNodeOrReward(name) {
                if ( name === 'node' ) {
                    this.nodePage = true
                    this.rewardPage = false
                } else if ( name === 'reward' ) {
                    this.nodePage = false
                    this.rewardPage = true
                }else{
                    this.nodePage = true
                    this.rewardPage = false
                }
            },
            tableSortChange(co){
                if (co === "last_season_reward"){
                    if (this.seasontype === "ascending") {
                        this.seasontype = "descending"
                        this.sorttype = "season_desc"
                        $(".item-reward").find(".season").find("img").attr("src", "../../static/img/desc.png")
                    }else {
                        this.seasontype = "ascending"
                        this.sorttype = "season_asc"
                        $(".item-reward").find(".season").find("img").attr("src", "../../static/img/asc.png")
                    }
                    $(".item-reward").find(".reward").find("img").attr("src", "../../static/img/def.png")
                    this.getRewardList()
                }else {
                    if (this.rewardtype === "ascending") {
                        this.rewardtype = "descending"
                        this.sorttype = "expected_desc"
                        $(".item-reward").find(".reward").find("img").attr("src", "../../static/img/desc.png")
                    }else {
                        this.rewardtype = "ascending"
                        this.sorttype = "expected_asc"
                        $(".item-reward").find(".reward").find("img").attr("src", "../../static/img/asc.png")
                    }
                    $(".item-reward").find(".season").find("img").attr("src", "../../static/img/def.png")
                    this.getRewardList()
                }
                this.reload()
            },
            reload () {
                this.isRouterAlive = false
                this.$nextTick(() => (this.isRouterAlive = true))
            }
        }
}


</script>


<style scoped>
    .m-content{
        padding: 1rem;
        background: #E6E9EE;
    }
    .m-head{
        background: #ffffff;
    }
    .m-head .m-item{
        text-align: center;
        border-bottom: 1px solid #F0F0F0;
        padding: 1rem 0;
    }
    .m-item p{
        color: #888888;
        font-size: 12px;
    }
    .item-lebel{
        font-size: 17px;
        margin-bottom: .5rem;
    }
    .m-tips{
        padding: .7rem;
        text-align: left;
    }
    .m-tips .el-button {
      font-size:14px;
      font-weight:400;
      color:rgba(136,136,136,1);
      border: none;
      background: none;
    }
    .navtitle {
      color:rgba(0,93,166,1) !important;
    }
    .pos-list .item{
        background: #ffffff;
        border-radius:4px;
        padding: 1.5rem .8rem;
        margin-bottom: .8rem;
    }
    .item-head {
        padding-bottom: 1rem;
        border-bottom: 1px solid #F0F0F0;
    }
    .item-head .head-info{
        line-height:20px;
        display: flex;
        justify-content: space-between;
        margin-bottom: .6rem;
    }
    .item-head .lable{
        color: #888888;
        font-size:12px;
        margin-right: 1rem;
        width: 50px;
        text-align: left;
    }
    .item-head .lable-reward{
        color: #888888;
        font-size:12px;
        margin-right: 1rem;
        width: 130px;
        text-align: left;
    }
    .item-head .con{
        flex: 1;
        word-break:break-all;
        text-align: left;
    }
    .item-info{
        padding-top: 1rem;
    }
    .item-info .con-head{
        display: flex;
        justify-content: space-between;
        color: #888888;
    }
    .item-info .con-head span{
    }
    .item-info .con-head .time{
        width:150px;
    }
    .item-info .item-con{
        font-size:14px;
        color: #000000;
        margin-bottom: 0.9rem;
    }
    .item-info .item-con .time{
        width:150px;
        text-align: center;
    }

    .item-reward{
        padding-top: 1rem;
    }
    .item-reward .con-head{
        display: flex;
        justify-content: space-between;
        color: #888888;
    }
    .item-reward .item-con{
        font-size:14px;
        color: #000000;
        margin-bottom: 0.9rem;
    }
</style>



